import { email, required, minLength, maxLength } from 'vuelidate/lib/validators';

export default {
  name: 'SignInPage',
  data() {
    return {
      email: '',
      password: '',
      isPwd: false,
      errorMessage: null,
    }
  },
  validations: {
    email: {
      required,
      email,
      maxLength: maxLength(30)
    },
    password: {
      required,
      minLength: minLength(8),
      maxLength: maxLength(20)
    },
  },
  computed: {
    emailErrorMessage () {
      if (!this.$v.email.required) {
        return 'Preencha seu email'
      } else if (!this.$v.email.email) {
        return 'Por favor insira um email válido'
      }

      return ''
    },
    passwordErrorMessage () {
      if (!this.$v.email.required) {
        return 'Informe sua senha'
      } else if (!this.$v.password.minLength) {
        return 'Mínimo de 8 dígitos'
      } else if (!this.$v.password.maxLength) {
        return 'Máximo de 15 dígitos'
      }

      return ''
    },
  },
  methods: {
    async submit() {
      if (!this.$v.$anyError) {
        await this.$store.dispatch('users/retrieveToken', { credentials: {
          email: this.email,
          password: this.password,
        }}).then((response) => {
            this.$router.push({ name: 'Profile' });
        }).catch((error) => {
          if(error.message === 'Request failed with status code 400') {
            this.errorMessage = 'Não encontramos uma conta com esse email';
          }
          if (error.message === 'Request failed with status code 401') {
            this.errorMessage = 'Email ou senha inválidos';
          }
          if (error.message === 'timeout of 5000ms exceeded') {
            this.errorMessage = 'Houve um Problema, tente novamente';
          }
          console.log('signIn/submit', error.message);
        })
      }
    },
  },
}
